* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box, .country-list {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

#search-box {
  font: inherit;
  background-color: ffffff;
  z-index: 999;
  border: 1px solid #bbb;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding: 8px;
  transition: box-shadow, border .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 5px 0 40px -5px rgba(0, 0, 0, .1);
}

#search-box:hover, #search-box:focus, #search-box:active {
  border: 1px solid #3462b8;
  box-shadow: 0 5px 40px -5px rgba(96, 112, 255, .39);
}

.country-list {
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 8px;
  list-style-type: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 0 40px -5px rgba(0, 0, 0, .1);
}

.country-list:hover, .country-list:focus {
  box-shadow: 0 5px 40px -5px rgba(96, 112, 255, .39);
}

.country-list-item {
  display: flex;
}

.country-list-item:not(:last-child) {
  margin-bottom: 8px;
}

.country-list-item > img {
  margin-right: 5px;
}

.country-info {
  max-width: 60%;
  background-color: #fff;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 0 40px -5px #ccc;
}

.country-info:hover, .country-info:focus {
  box-shadow: 0 0 40px -5px #9b9b9b;
}

.country-info > img {
  width: 100%;
  height: auto;
  margin-bottom: 8px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.country-info-content:not(:last-child) {
  margin-bottom: 8px;
}

